import React from 'react'

import {
  CAccordion,
  CAccordionBody,
  CAccordionButton,
  CAccordionCollapse,
  CAccordionHeader,
  CAccordionItem,
  CAvatar,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCardTitle,
  CCol,
  CHeader,
  CProgress,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from '@coreui/react'

import data from '../../assets/data.json'
import weight from '../../assets/weight.json'
import PropTypes from 'prop-types'

const get_paths = (items, id) => {
  // Get all paths
  let paths_twice = []
  let paths_thrice = []
  let companies = [items[id].company, null]
  let strategies = [...items[id].strategy, null]
  let tactics = [...items[id].tactic, null]
  for (let i in companies) {
    let count1 = companies[i] === null ? 1 : 0
    for (let j in strategies) {
      let count2 = strategies[j] === null ? count1 + 1 : count1
      for (let k in tactics) {
        let count3 = tactics[k] === null ? count2 + 1 : count2
        if (count3 === 1) {
          paths_twice.push([companies[i], strategies[j], tactics[k]])
        } else if (count3 === 0) {
          paths_thrice.push([companies[i], strategies[j], tactics[k]])
        }
      }
    }
  }

  return { paths_twice, paths_thrice }
}

const get_blueprints = (stages) => {
  let names = []
  let weight_sum = 0
  for (let i in data) {
    if (
      (stages[0] == null || data[i].company === stages[0]) &&
      (stages[1] == null || data[i].strategy.includes(stages[1])) &&
      (stages[2] == null || data[i].tactic.includes(stages[2]))
    ) {
      let name = data[i].name
      let subtype = data[i].subtype != null ? data[i].subtype : ''
      let cls = data[i].class
      let w = weight[cls]
      names.push([name, subtype, cls, w])
      weight_sum += w
    }
  }

  for (let i in names) {
    names[i][3] = names[i][3] / weight_sum
  }
  return names
}

const Dashboard = (props) => {
  Dashboard.propTypes = {
    index: PropTypes.number,
  }

  if (props.index < 0) {
    return
  }

  let items_twice = []
  let items_thrice = []
  let paths = get_paths(data, props.index)
  let paths_twice = paths.paths_twice
  let paths_thrice = paths.paths_thrice

  for (let i in paths_twice) {
    let blueprints = get_blueprints(paths_twice[i])
    let rows = []
    for (let j in blueprints) {
      rows.push(
        <CTableRow>
          <CTableDataCell>{blueprints[j][0]}</CTableDataCell>
          <CTableDataCell>{blueprints[j][1]}</CTableDataCell>
          <CTableDataCell>{blueprints[j][3]}</CTableDataCell>
        </CTableRow>,
      )
    }

    let name = paths_twice[i][0] === null ? '' : paths_twice[i][0]
    name =
      name +
      (name === '' || paths_twice[i][1] === null ? '' : ' + ') +
      (paths_twice[i][1] === null ? '' : paths_twice[i][1])
    name = name + (paths_twice[i][2] === null ? '' : ' + ' + paths_twice[i][2])

    items_twice.push(
      <CAccordionItem itemKey={i}>
        <CAccordionHeader>{name}</CAccordionHeader>
        <CAccordionBody>
          <CTable bordered striped hover>
            <CTableBody>{rows}</CTableBody>
          </CTable>
        </CAccordionBody>
      </CAccordionItem>,
    )
  }

  for (let i in paths_thrice) {
    let blueprints = get_blueprints(paths_thrice[i])
    let rows = []
    for (let j in blueprints) {
      rows.push(
        <CTableRow>
          <CTableDataCell>{blueprints[j][0]}</CTableDataCell>
          <CTableDataCell>{blueprints[j][1]}</CTableDataCell>
          <CTableDataCell>0.00%</CTableDataCell>
        </CTableRow>,
      )
    }

    items_thrice.push(
      <CAccordionItem itemKey={i}>
        <CAccordionHeader>
          {paths_thrice[i][0]} + {paths_thrice[i][1]} + {paths_thrice[i][2]}
        </CAccordionHeader>
        <CAccordionBody>
          <CTable bordered striped hover>
            <CTableBody>{rows}</CTableBody>
          </CTable>
        </CAccordionBody>
      </CAccordionItem>,
    )
  }

  return (
    <>
      <CCard className="mb-4">
        <CCardHeader component="h4">两次研究</CCardHeader>
        <CCardBody>
          <CAccordion>{items_twice}</CAccordion>
        </CCardBody>
      </CCard>
      <CCard className="mb-4">
        <CCardHeader component="h4">三次研究</CCardHeader>
        <CCardBody>
          <CAccordion>{items_thrice}</CAccordion>
        </CCardBody>
      </CCard>
    </>
  )
}

export default Dashboard
