import { React, useState } from 'react'
import { NavLink, useLocation } from 'react-router-dom'
import { CNavGroup, CNavItem, CNavLink, CSidebarNav } from '@coreui/react'
import PropTypes from 'prop-types'

import { CBadge } from '@coreui/react'
import data from '../assets/data.json'

export const AppSidebarNav = (props) => {
  AppSidebarNav.propTypes = {
    handleClick: PropTypes.func,
  }

  let map = new Array()
  map['航母'] = []
  map['战巡'] = []
  map['巡洋'] = []
  map['驱逐'] = []
  map['护卫'] = []
  map['护航艇'] = []
  map['战机'] = []

  for (let i in data) {
    if (data[i].name in map[data[i].class]) {
      map[data[i].class][data[i].name].push(i)
    } else {
      map[data[i].class][data[i].name] = [i]
    }
  }

  let groups = []
  for (let i in map) {
    let items = []
    for (let j in map[i]) {
      let name = data[map[i][j][0]].name
      if (map[i][j].length === 1) {
        items.push(
          <CNavItem>
            <CNavLink href="javascript:void(0);" onClick={() => props.handleClick(map[i][j][0])}>
              {name}
            </CNavLink>
          </CNavItem>,
        )
      } else {
        let sub_items = []
        for (let k in map[i][j]) {
          sub_items.push(
            <CNavItem>
              <CNavLink href="javascript:void(0);" onClick={() => props.handleClick(map[i][j][k])}>
                {data[map[i][j][k]].subtype}
              </CNavLink>
            </CNavItem>,
          )
        }
        items.push(<CNavGroup toggler={data[map[i][j][0]].name}>{sub_items}</CNavGroup>)
      }
    }
    groups.push(<CNavGroup toggler={i}>{items}</CNavGroup>)
  }

  return <CSidebarNav>{groups}</CSidebarNav>
}
